import Vue from 'vue'
import VueRouter from 'vue-router'
import Timetable from '../pages/Timetable.vue'
import About from '../pages/About.vue'
import Add from '../pages/Add.vue'
import Archive from '../pages/Archive.vue'
import Popup from '../components/Popup.vue'

Vue.use(VueRouter)

// Router routes configuration

const routes = [
  {
    path: '/',
    name: 'Timetable',
    component: Timetable,
    props: true,
    children: [
      {
        path: '/event/:id',
        component: Popup,
        props: { type: 'timetable'}
      },
    ]
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    props: true,
    children: [
      {
        path: '/about'
      },
    ]
  },
  {
    path: '/add',
    name: 'Add',
    component: Add,
    props: true
  },
  {
    path: '/archive',
    name: 'Archive',
    component: Archive,
    props: true,
    children: [
      {
        path: '/event/:id',
        component: Popup,
        props: { type: 'archive'}
      },
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router